import routes from "../../routers"; // 
import store from "../../vuex/store";
import { mapState } from "vuex";
import { savaToLocal, loadFromlLocal } from "./storage.js"; // 
import FastClick from "fastclick";
import { formatDate } from "./date.js";
import $ from "jquery";

window.getCookie = function getCookie(name) {
  var arr,
    reg = new RegExp("(^| )" + name + "=([^;]*)(;|$)");

  if ((arr = document.cookie.match(reg))) return unescape(arr[2]);
  else return null;
};

window.setCookie = function setCookie(name, value) {
  var Days = 30;
  var exp = new Date();
  exp.setTime(exp.getTime() + Days * 24 * 60 * 60 * 1000);
  document.cookie =
    name + "=" + escape(value) + ";expires=" + exp.toGMTString();
};

window.setDocumentTitle = title => {
  document.title = title;
  if (/ip(hone|od|ad)/i.test(navigator.userAgent)) {
    var i = document.createElement("iframe");
    i.src = "/favicon.ico";
    i.style.display = "none";
    i.onload = function() {
      setTimeout(function() {
        i.remove();
      }, 9);
    };
    document.body.appendChild(i);
  }
};

// 
$(document).ready(function() {
  $("body").height($("body")[0].clientHeight);
});

//300ms
var str = navigator.userAgent.toLowerCase();
var ver = str.match(/cpu iphone os (.*?) like mac os/);
if (!ver) {
  //IOS
  // fastclick
  FastClick.attach(document.body);
  store.commit("UPDATA_SYSTEM", "Android");
} else {
  console.log("Ios" + ver[1].replace(/_/g, "."));
  store.commit("UPDATA_SYSTEM", "ios");
  if (parseInt(ver[1]) >= 11) {
    //fastclick
  } else {
    // fastclick
    FastClick.attach(document.body);
  }
}

export default {
  install(Vue, options) {
    Vue.prototype.updatateUserInfo = function(backfun = null) {
      //

      Vue.prototype.$http
        .post(store.state.path + `php/ttl/userFunction.php`, {
          type: "userinfo",
          openid: store.state.openid
        })
        .then(response => {
          let data = response.body;
          if(data.error=='ok'){
            if (data.phone == null) data.phone = "";
            store.commit("UPDATE_PHONENO", data.phone);
            store.commit("UPDATE_ROLE", data.role.toString());
            //
            if(data.role.toString()=='1'||data.role.toString()=='6'){
              store.commit("UPDATA_BIND_STOREINFO", data.belongstore[0]);
            }
            //
            if(data.role.toString()=='2'){
              store.commit("UPDATA_STOREINFO", data.store[0]);
            }
            store.commit("UPDATE_ICON", data.headimgurl);
            store.commit("UPDATE_USERNAME", data.username);
            store.commit("UPDATE_ACCOUNTS", data.accounts);
            store.commit("UPDATE_CLIENTNUM", data.clientNum);
            store.commit("UPDATE_GIFTNUM", data.giftnum);
            store.commit("UPDATE_EXPNUM", data.expnum);
            store.commit("UPDATE_SOURCE", data.source);
            
          }else{
            // alert("data.error=ok");
          }

          if (backfun != null && backfun != undefined) {
            backfun();
          }
        })
        .catch(response=>{
          //
          console.log(response);
          // alert("catch");
        });
    };
    Vue.prototype.refreshInfoInTime = function(backfun = null) {
      //

      Vue.prototype.$http
        .post(store.state.path + `php/Route.php`, {
          type: "userinfoheart",
          uid: store.state.userid
        })
        .then(response => {
          let data = response.body;
          if(!data.role) return;
          store.commit("UPDATE_ROLE", data.role.toString());
          // store.commit("UPDATE_ROLE", "1");
          store.commit("UPDATE_SOURCE", data.source);
          store.commit("UPDATE_ACCOUNTS", data.accounts);
          // store.commit("UPDATE_GIFTNUM", data.giftnum);
          // store.commit("UPDATE_EXPNUM", data.expnum);

          if (backfun != null && backfun != undefined) {
            backfun();
          }
        })
        .catch(response=>{
          //
          console.log(response);
        });
    };
    //
    Vue.prototype.resetUserData = function() {
        savaToLocal("aml", "openid", "");
        savaToLocal("aml", "userid", "0");
        savaToLocal("aml", "token", "");
        store.commit("UPDATA_TOKEN", "");
        store.commit("UPDATE_OPNEID", "");
        store.commit("UPDATE_USERID", "0");

        store.commit("UPDATE_ROLE", "1");
        store.commit("UPDATE_ICON", "http://www.amyshirley.com/aml/img/headimg/aml_logo.png");
        store.commit("UPDATE_ACCOUNTS", 0);
        store.commit("UPDATE_GIFTNUM", 0);
        store.commit("UPDATE_EXPNUM", 0);
        store.commit("UPDATE_SOURCE", 0);
    };
  }
};



