import Vue from "vue";
import Vuex from "vuex";
// import * as actions from "./actions";
import * as getters from "./getters";
//  vue  vuex
Vue.use(Vuex);
let appLoading = document.getElementById("appLoading");

const state = {
    token: "",
    loadingShow: true, //loading
    consumeOkPopupShow: false, //
    receiveOkPopupShow: false, //
    path: "http://www.amyshirley.com/aml/", // http://192.168.2.232/amlqs/   http://www.amyshirley.com/aml/  http://www.amyshirley.com/aml/
    phoneno: "", // ""
    role: "1", //123
    icon: "http://www.amyshirley.com/aml/img/headimg/aml_logo.png", //
    openid: "", //openid
    userid: "1", //id
    username: "", //
    gender: "", //nannv
    curMealData: {}, //
    accounts: 0, //
    clientNum: 0, //
    giftnum: 0, //
    expnum: 0, //
    seturl: "",
    source: 0, //
    wxappid: "wxd42787e75e0a0d24",
    domainUrl: "http://www.amyshirley.com/aml/", //
    paramMap: {}, // key=URL value= url
    agencySalesmanArr: null, //
    agencyTJTArr: null, //
    therapistList:[], //
    sistem: "", //ios|Android
    storeInfo:null,  // 
    bindStoreInfo:null // 
};

let loadingClearId = -1000; //loading clearID
let loadingMark = false; //loading
let mustId = -1000; //loadingclearID

const actions = {
  toggleLoading(context, value) {
    //loading200ms,
    loadingMark = value;
    if (value) {
      if (state.loadingShow) return;
      if (loadingClearId != -1000) return;
      loadingClearId = window.setTimeout(() => {
        window.clearTimeout(loadingClearId);
        loadingClearId = -1000;
        context.commit("UPDATE_LOADING", true);
        context.dispatch("mustShowLoading");
      }, 100);
    } else {
      if (state.loadingShow) {
        if (mustId != -1000) return;
        context.commit("UPDATE_LOADING", false);
      } else {
        if (loadingClearId != -1000) {
          window.clearTimeout(loadingClearId);
        }
      }
    }
  },
  mustShowLoading(context) {
    //loading500ms
    mustId = window.setTimeout(() => {
      window.clearTimeout(mustId);
      mustId = -1000;
      if (!loadingMark) context.commit("UPDATE_LOADING", false);
    }, 400);
  }
};
//  mutation 
const mutations = {
  //token
  UPDATA_TOKEN(state, value) {
    state.token = value;
  },
  //storeInfo
   UPDATA_STOREINFO(state, value) {
    state.storeInfo = value;
  },
  //bindStoreInfo
  UPDATA_BIND_STOREINFO(state, value) {
    state.bindStoreInfo = value;
  },
  UPDATE_LOADING(state, value) {
    //loading
    state.loadingShow = value;
    if (value) {
      if (!appLoading.classList.contains("hide")) return;
      appLoading.classList.remove("hide");
    } else {
      if (appLoading.classList.contains("hide")) return;
      appLoading.classList.add("hide");
    }
  },
  UPDATE_CONSUMEOKSHOW(state, value) {
    //
    state.consumeOkPopupShow = value;
  },
  UPDATE_RECEIVEOKSHOW(state, value) {
    //
    state.receiveOkPopupShow = value;
  },
  UPDATE_PHONENO(state, value) {
    //
    state.phoneno = value;
  },
  UPDATE_ROLE(state, value) {
    //
    state.role = value;
  },
  UPDATE_ICON(state, value) {
    //
    state.icon = value;
  },
  UPDATE_OPNEID(state, value) {
    //openid
    state.openid = value;
  },
  UPDATE_USERID(state, value) {
    //userid
    state.userid = value;
  },
  UPDATE_USERNAME(state, value) {
    //username
    state.username = value;
  },
  UPDATE_GENDER(state, value) {
    //
    state.gender = value;
  },
  UPDATE_CURMEALDATA(state, value) {
    //
    state.curMealData = value;
  },
  UPDATE_ACCOUNTS(state, value) {
    //
    state.accounts = value;
  },
  UPDATE_CLIENTNUM(state, value) {
    //
    state.clientNum = value;
  },
  UPDATE_GIFTNUM(state, value) {
    //
    state.giftnum = value;
  },
  UPDATE_EXPNUM(state, value) {
    //
    state.expnum = value;
  },
  UPDATE_SOURCE(state, value) {
    state.source = value;
  },
  SETURL(state, value) {
    //
    state.seturl = value;
  },
  REFRESHROUTERPARAM(state, paramKV) {
    //
    Vue.set(state.paramMap, paramKV.key, paramKV.value);
  },
  UPDATA_A_SALEMAN(state, value) {
    //
    state.agencySalesmanArr = value;
  },
  UPDATA_THERAPIST_LIST(state, value) {
    //
    state.therapistList = value;
  },
  UPDATA_A_TJT(state, value) {
    //
    state.agencyTJTArr = value;
  },
  UPDATA_SYSTEM(state, value) {
    //
    state.sistem = value;
  }
};

//  store
//  store 
export default new Vuex.Store({
  state,
  mutations,
  actions,
  getters
});



